let Config = {
  // 
  notExport: 1, // don't export
  export: 2, // export
  exportDefault: 3, // export default
  globalExportMode: 1, // don't export
  lineBreak: "\n", // 
  indent: "  ", //  
  interfaceName: "Result", // 
  type: "interface", //  interface | type
  interfaceNamePrefix: "I", // 
  normalTypes: ["string", "number", "boolean"], // 
};
// 
let objs = [];
let interfaceNames = [];

function _isObject(obj) {
  return Object.prototype.toString.call(obj) === "[object Object]";
}

function _isArray(obj) {
  return Object.prototype.toString.call(obj) === "[object Array]";
}

/**
 * 
 * @param name key
 * @returns {string} 
 */
function _getOnlyInterfaceName(name) {
  if (!interfaceNames.includes(name)) {
    return name;
  }
  // 
  let lastCharacter = name.slice(-1);
  if (lastCharacter >= "0" && lastCharacter <= "9") {
    lastCharacter = parseInt(lastCharacter) + 1;
    return _getOnlyInterfaceName(
      name.substring(0, name.length - 1) + lastCharacter
    );
  } else {
    return _getOnlyInterfaceName(name + "1");
  }
}

function _getBaseName(key) {
  const firstName = key.substring(0, 1);
  const lastName = key.substring(1);
  return firstName.toUpperCase() + lastName;
}

function _getInterfaceName(key) {
  const arr = key.split("_");
  for (let i = 0; i < arr.length; i++) {
    arr[i] = _getBaseName(arr[i]);
  }
  let fullName = arr.join("");
  fullName =
    Config.interfaceNamePrefix + Config.interfaceName + _getBaseName(fullName);
  fullName = _getOnlyInterfaceName(fullName);
  interfaceNames.push(fullName);
  return fullName;
}

/**
 * 
 * @param {*} type
 * @returns
 */
function __getType(type) {
  return type === "type" ? " =" : "";
}

/**
 * 
 * @param name
 * @returns {string}
 * @private
 */
function _getRenderInterface(name) {
  if (
    (Config.globalExportMode === Config.exportDefault &&
      name === Config.interfaceNamePrefix + Config.interfaceName) ||
    name === Config.interfaceNamePrefix + Config.interfaceName
  ) {
    // export default 
    return `${
      Config.globalExportMode === Config.export
        ? "export "
        : Config.globalExportMode === Config.exportDefault
        ? "export default "
        : ""
    }${Config.type}`;
  }
  return `${Config.globalExportMode === Config.export ? "export " : ""}${
    Config.type
  }`;
}

function __getRenderInterfaceName(name) {
  return name;
}

function _getRenderLeft() {
  return `{${Config.lineBreak}`;
}

function _getRenderRight() {
  return `}${Config.lineBreak}`;
}

function _getRenderKey(key) {
  return key;
}

function _getRenderValue(value) {
  return ` ${value};${Config.lineBreak}`;
}

/**
 * 
 * @param arr
 * @returns {string}
 */
function _isBaseType(arr) {
  // 
  let type = typeof arr[0];
  for (let i = 1; i < arr.length; i++) {
    if (type !== typeof arr[i]) {
      return "any";
    }
  }
  return type;
}

/**
 * 
 * @param json json
 * @param key key
 * @param inters 
 * @param indent 
 * @returns {*}
 */
function _handleArray(json, key, inters, indent) {
  if (json[key].length === 0) {
    inters += `${indent}${_getRenderKey(key)}:${_getRenderValue("any[]")}`;
  } else {
    // 
    if (_isArray(json[key][0])) {
      // boolean number string
      inters += `${indent}${_getRenderKey(key)}:${_getRenderValue("any[]")}`;
    } else {
      // 
      // any
      //  [1,2,3]
      if (Config.normalTypes.includes(typeof json[key][0])) {
        const type = _isBaseType(json[key]);
        inters += `${indent}${_getRenderKey(key)}: ${_getRenderValue(
          type + "[]"
        )}`;
      } else {
        const interfaceName = _getInterfaceName(key);
        inters += `${indent}${_getRenderKey(key)}: ${_getRenderValue(
          interfaceName + "[]"
        )}`;
        objs.push({
          key: interfaceName,
          value: json[key][0],
        });
      }
    }
  }
  return inters;
}

/**
 * json
 * @param json json
 * @param name 
 * @param inters 
 * @param first 
 * @param ind  tab
 * @returns {*}
 */
function _parseJson(json, name, inters, first = true, ind = Config.indent) {
  const keys = Reflect.ownKeys(json);
  if (!keys.length) {
    // key
    inters += `${_getRenderInterface(name)} ${__getRenderInterfaceName(
      name
    )}${__getType(Config.type)} ${_getRenderLeft()}`;
    inters += _getRenderRight();
    return inters;
  }
  if (!inters && first) {
    inters += `${_getRenderInterface(name)} ${__getRenderInterfaceName(
      name
    )}${__getType(Config.type)} ${_getRenderLeft()}`;
  } else if (!inters && !first) {
    inters += _getRenderLeft();
  }
  let type;
  for (const key of keys) {
    // 
    type = typeof json[key];
    if (Config.normalTypes.includes(type)) {
      inters += `${ind}${_getRenderKey(key)}:${_getRenderValue(type)}`;
    } else if (_isArray(json[key])) {
      inters = _handleArray(json, key, inters, ind);
    } else if (_isObject(json[key])) {
      // inters += `${ind}${_getRenderKey(key)}: ${_parseJson(json[key], key, '', false, ind + ind)}`;
      const interfaceName = _getInterfaceName(key);
      inters += `${Config.indent}${_getRenderKey(key)}: ${interfaceName};${
        Config.lineBreak
      }`;
      objs.push({
        key: interfaceName,
        value: json[key],
      });
    }
  }
  if (first) {
    inters += _getRenderRight();
  } else {
    inters += Config.indent + _getRenderRight();
  }
  return inters;
}

/**
 * 
 * @param res json
 * @param options
 * @returns {*} 
 */
module.exports = function interfaceDefinition(res, options = {}) {
  // 
  Config = Object.assign(Config, options);
  let result;
  objs = [];
  interfaceNames = [];
  try {
    const json = typeof res === "string" ? JSON.parse(res) : res;
    result = _parseJson(json, _getInterfaceName(""), "", true);
    for (const obj of objs) {
      result += Config.lineBreak;
      result += _parseJson(obj.value, obj.key, "", true);
    }
  } catch (e) {
    result = e.message;
  }
  return result;
};
